import axios from 'axios';
import { GET_ALL_DOGS, GET_DOG_ID, GET_DOG_NAME, CREATE_DOG, GET_TEMPER, A_TO_Z, WEIGHT} from './actions';

export  function getAllDogs(){
    return  async function(dispatch){
        return await axios(`http://localhost:3001/dog`)
        .then(res => {
            dispatch({type: GET_ALL_DOGS, payload: res.data})
        })
        .catch((e) => console.log(e))
    }
}
export function getDogByName(name){
    return function(dispatch){
        return axios(`http://localhost:3001/dog?name=${name}`)
        .then(res => {
            dispatch({type: GET_DOG_NAME, payload: res.data})
        })
        .catch((e) => console.log(e))
    }
}
export function getDogID(name){
    return function(dispatch){
        return axios(`http://localhost:3001/dog/${name}`)
        .then(data => {
            dispatch({type:GET_DOG_ID, payload: data})
        })
        .catch((e) => console.error(e))
    }
}

export function getDogName(name){
    return function(dispatch){
        return axios(`http://localhost:3001/dog?name=${name}`)
        .then(res => {
            dispatch({type: GET_DOG_NAME, payload: res.data})
        })
    }
}
export function getTemper(){
    return function(dispatch){
        return axios(`http://localhost:3001/temper`)
        .then(resp =>{
            dispatch({ type: GET_TEMPER, payload: resp.data});
        })
        .catch((e)=>console.log(e))
    }
}
export function raceCreator(data){
    return function(dispatch){
        return axios.post(`http://localhost:3001/dog`, data)
        .then(resp =>{
            dispatch({type: CREATE_DOG, payload: resp.data });
        })
        .catch((e)=> console.log(e))
    };
};
export function orderByName(name, sort){
    return function(dispatch){
        dispatch({type: A_TO_Z, payload: {name, sort}})
    };
};
export function orderByWeight(data){
    return function(dispatch){
        dispatch({type: WEIGHT, payload: data})
    }
}
