import React, { useEffect } from 'react';
import {useDispatch, useSelector} from 'react-redux';
import { useParams } from 'react-router-dom';
import { getDogByName } from '../../redux/actions/actionsCreator';

function DogDetail() {
    const dispatch = useDispatch();
    const { name } = useParams();
    const { data } = useSelector(state => state.dogsDetail);
    useEffect(()=>{
        dispatch(getDogByName(name))
    }, [dispatch, name])
    return (
    <div>
        <h3>Name: {data.name}</h3>
        <p>Weight: {data.weight} kgs.</p>
        <p>Height: {data.height} cms.</p>
        <p>Temperament: {data.temperament}</p>
        <p>Life Span: {data.life_span}</p>
        <img src={data.img} alt="dogImage"/>
    </div>
  )
}

export default DogDetail